* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Inter', sans-serif;
}
.nav-bar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    background: #21222A;
    height: 91px;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap:10px;
    color: #61DAFB;
}
.nav-logo .img-logo{
    width: 28px;
}
.nav-logo .text-logo{
    font-size: 22px;
    font-weight: 700;
}
.nav-title{
    color: #DEEBF8;
    font-size: 16px;
    font-weight: 600;
}
.main-content{
    position: relative;
    padding: 0 20px;
    background-color: #282D35;
    color: #FFFFFF;
    height: calc(100vh - 91px);
    z-index: 1;
}
.main-title{
    margin: 0;
    padding: 40px 0;
    font-size: 40px;
    font-weight: 700;
}
.facts-list{
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin: 0;
    list-style: none;
}
.facts-list >*{
    font-size: 16px;
    position: relative;
}
.facts-list >*::before{
    content: "";
    position: absolute;
    left: -25px;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: #61DAFB;
}
.background-react-logo{
    position: absolute;
    right: 0;
    top: 45px;
    z-index: -1;
}